.radiogroup input[type=radio] + label:first-of-type {
  border-left: 1px solid black;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.radiogroup input[type=radio] + label:last-of-type {
  border-right: 1px solid black;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
input[type=radio]:checked + label {
  background: #cacaca;
  padding-top: 7px;
  padding-bottom: 3px;
  position: relative;
  top: 2px;
  -webkit-box-shadow: inset 0px 1px 1px 1px #292929b5, 1px 1px 1px -1px #292929b5;
          box-shadow: inset 0px 1px 1px 1px #292929b5, 1px 1px 1px -1px #292929b5;
}
.controls {
  display: grid;
  width: 300px;
  grid-gap: 10px;
  margin: 20px 0px;
}
.controls > p {
  margin: 0px;
}
.config {
  width: 600px;
  margin: 0 auto;
  padding: 10px;
}
h2 {
  margin: 10px 0px;
}
.font-config {
  margin: 5px 0px;
}
.font-config > label {
  display: grid;
  text-align: right;
  grid-template-columns: 175px -webkit-max-content;
  grid-template-columns: 175px max-content;
  grid-gap: 10px;
  width: 200px;
  margin-bottom: 10px;
}
.color-config {
  display: grid;
  grid-template-columns: 250px 250px;
  grid-gap: 10px;
  justify-items: center;
}
.info {
  width: 600px;
